
ks-product-fav-card {
    display: flex;
    align-items: stretch;
    flex-wrap: nowrap;
}

ks-product-fav-card > a {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;

    text-decoration: none !important;
    color: #000 !important;
}

ks-product-fav-card .image {
    height: 100%;
    width: 100%;
    max-height: 180px;
    max-width: 180px;

    margin-right: 10px;
    padding: 10px;
    box-sizing: border-box;
}

ks-product-fav-card h3 {
    font-family: var(--font-emphasis);
    margin-bottom: 5px;
    font-size: 20px;
}

ks-product-fav-card p {
    font-size: 15px;
}

ks-product-fav-card .price {
    font-size: 18px;
    font-family: var(--font-emphasis);
}

ks-product-fav-card .price .current {
    font-weight: 700;
    color: var(--color-secondary);
}

ks-product-fav-card .price .previous {
    margin: 0px 10px;
    color: #909090
}

ks-product-fav-card .buttons {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin-left: 10px;
}

ks-product-fav-card .buttons button {
    background-color: var(--color-secondary);
    color: white;
    border-style: none;
    width: 50px;
    height: 100%;
    position: relative;

    transition: background-color 0.3s ease;
}

ks-product-fav-card .buttons button:hover {
    background-color: var(--color-secondary-hover);
}

ks-product-fav-card .buttons button:active {
    background-color: var(--color-secondary-active);
}

ks-product-fav-card .buttons .fav {
    border-radius: 10px 10px 0 0 ;
}

ks-product-fav-card .buttons .cart {
    border-top: solid 1px var(--color-secondary-darker);
    border-radius: 0 0 10px 10px;
}

ks-product-fav-card .buttons .cart span {
    display: none;
}

ks-product-fav-card[unavailable] .buttons .cart {
    border-top: none;
    background-color: var(--color-faded);
    color: var(--color-faded-text);
}


@media screen and (max-width: 750px) {
    ks-product-fav-card p {
        display: none;
    }
}

@media screen and (max-width: 550px) {
    ks-product-fav-card {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    ks-product-fav-card > a {
        flex-direction: column;
        align-items: center;
    }

    ks-product-fav-card .image {
        margin: 0;
    }

    ks-product-fav-card .buttons {
        flex-direction: row;
        margin: 0;
        width: 100%;
        max-width: 240px;
        margin-top: 10px;
    }

    ks-product-fav-card .buttons button {
        height: 40px;
    }

    ks-product-fav-card .buttons .fav {
        flex: 1 0 40px;
        width: 40px;
        border-radius: 100px;
        margin-right: 5px;
    }

    ks-product-fav-card .buttons .cart {
        width: 100%;
        border-radius: 10px;
    }

    ks-product-fav-card .buttons .cart ks-icon {
        display: none;
    }

    ks-product-fav-card .buttons .cart span {
        display: inline;
    }
}